.feedback-form {
  max-width: 480px;
  background-color: #a4c0d1;
  border: none;
  border-radius: 10px;
  flex-direction: column;
  margin: 0 auto;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  background-color: #87cefa;
  border: none;
  border-radius: 10px;
  padding: 8px 12px;
  transition: background-color .3s linear;
}

.feedback-form button:hover, .feedback-form button:focus {
  background-color: #46a0d8;
}

input, textarea {
  font: inherit;
  background-color: #dee4e7;
  border: none;
  border-radius: 10px;
  padding: 8px;
}

textarea {
  resize: none;
}

